main {
  display: flex;
  height: 70vh;
  width: 100vw;
  position: fixed;
}

.container-left {
  height: 100vh;
  width: 60vw;
  padding-left: 135px;
  margin-top: 25%;
}

.container-left h1 {
  font-weight: 400;
  font-size: 70px;
  line-height: 80px;
  color: #5D5D7D;
  width: 368px;
  margin-bottom: 15px;
}

.container-left p {
  font-weight: 400;
  font-size: 28px;
  line-height: 48px;
  color: #5D5D7D;
  width: 540px;
  margin-bottom: 15px;
}

.container-left button {
  width: 269px;
  height: 62px;
  background: #9499EA;
  box-shadow: 0px 10px 24px rgba(148, 153, 234, 0.0979959);
  border: none;
  border-radius: 50px;
  color: #fff;
  font-size: 24px;
}

.container-left button:active {
  background-color: #9499EA;
  border-radius: 50px;
}

.container-left button:hover {
  opacity: 0.8;
}


.container-right {
  height: 100vh;
  width: 40vw;
  background-color: #E5E5EA;
  padding: 15px;
}


.container-right header {
  margin-top: 15px;
}

.container-right a:active {
  color: rgb(53, 44, 78);
  font-size: 20px;
  outline: none;
}

/* .container-right a:hover {
  font-size: 26px;

} */

.container-right img {
  position: relative;
  margin-top: 30%;
  right: 120px;
  width: 50%;
}

.container-right ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  font-weight: 400;
  font-size: 24px;
  line-height: 41px;
  color: #5D5D7D;
}

@media (max-width: 768px) {
  .container-left {
    width: 50vw;
    padding-left: 20px;
    margin-top: 90%;
    
  }

  .container-right img {
    position: relative;
    margin-top: 60%;
    right: 53%;
    width: 105%;
  }

  .container-right header {
    position: absolute;
    left: 0%;
    width: 100%;
  }

  .container-left h1 {
    font-size: 40px;
    width: 195%;
  }

  .container-left p {
    width: 200%;
  }

  .container-right {
    width: 50vw;
    background-color: rgb(229, 229, 234, 0.2)
  }

  .container-left button {
    left: 30%;
    position: relative;
    bottom: -30px;
  }

}